import React from 'react';
import {Col,Nav} from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

const Product = (props) => {
    // let {title,imgUrl,price} = props.gagu;
    let navigate=useNavigate();
    let {gagu,key,res}=props;

  return (
    <Col md={3}>
      <Nav.Link onClick={()=>
      navigate('/detail/'+res[key])}>
        <img src={gagu.imgUrl} width="80%" alt="item" />
        <h4>{gagu.title}</h4>
        <p>{gagu.price}</p>
        </Nav.Link>
    </Col>
  )
}

export default Product