import React from 'react'
import { Col } from 'react-bootstrap'

const Chair = (props) => {
    const {chair} = props
  return (
    <Col md={3}>
        <img src="chair.imgUrl" width="80%" alt="item2"/>
        <h5>{chair.title}</h5>
        <p>{chair.content}</p>
        <p>{chair.price}</p>
    </Col>
  )
}

export default Chair