import React from 'react'
import { Col, Container, Row, Button } from 'react-bootstrap'
import { useParams } from 'react-router-dom';

const Detail = (props) => {
    let {gagu}=props;
    let {id}=useParams();
  return (
    <Container>
        <Row>
            <Col md={6}>
                <img src={gagu[id].imgUrl} width='100%'/>
            </Col>
            <Col md={6}>
                <h4 className='pt-5'>{gagu[id].title}</h4>
                <p>{gagu[id].content}</p>
                <p>{gagu[id].price}</p>
                <Button variant="outline-secondary"></Button>
            </Col>
        </Row>
    </Container>
  )
}

export default Detail